.modal {
    position: absolute;
    top: 50px;
    right: 80px;
    width: 200px;
    background: white;
    border: 1px solid #ccc;
    box-shadow: 1px 5px 10px rgb(211, 211, 216);
    -webkit-transition: 1.1s ease-out;
    transition: 1.1s ease-out;
    opacity: 1;
    visibility: visible;
  }
  .modal.off {
    opacity: 0;
    visibility: hidden;
    box-shadow: 1rem 0 0 rgba(0, 0, 0, 0.2);
  }